import React from 'react';
import './App.css';

import AJML from './AJML';


import {
    Switch,
    Route,
    BrowserRouter,
} from "react-router-dom";


function App() {
    return (
	<BrowserRouter basename={process.env.PUBLIC_URL}>
	<div>
	    <Switch>
              <Route exact path="/">
                <AJML />
	      </Route>
              <Route path="/ajml">
		<AJML />
	      </Route>
	    </Switch>
	</div>
	</BrowserRouter>
    );

}


export default App;
